.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}

.modal__form {
  position: relative;
  margin: auto;
  background-color: @white;
  padding: 35px 30px 46px 30px;
  width: 100%;
  border: 1px solid #c6c6c6;
  border-left: 0;
  border-right: 0;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);

  @media (max-width: @mobile-width-only) {
    margin-bottom: 225px;
  }

  @media (min-width: @tablet-width) {
    border-left: 1;
    border-right: 1;
    box-sizing: border-box;
    max-width: 566px;
    padding: 37.5px 108px 46px 108px;
    margin-bottom: 230px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 187px;
  }
}

.modal__name {
  margin: 0 -2px;
  text-align: center;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 57px;

    & br {
      display: none;
    }
  }
}

.modal__description {
  margin: 0;
  margin-top: 14px;
  text-align: center;
  font-size: 15px;
  line-height: 30px;

  @media (min-width: @tablet-width) {
    font-size: 17px;
  }
}

.modal__list {
  .no-markers-list;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 7px;
  margin: 0;
  margin-top: 21px;
}

.modal__item {
  font-size: 17px;
  line-height: 16px;
  height: 52px;
}

.modal__button {
  width: 100%;
  background-color: @downy;
  color: @white;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  border: 0;
  margin-top: 31px;
}

.modal__label {
  position: relative;
  width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
}

.modal__radio-mark {
  position: absolute;
  opacity: 0.1;
  border: 2px solid @tundora;
  width: 100%;
  height: 52px;

  &:hover {
    opacity: 0.3;
  }
}

.modal__radio-input:checked + .modal__radio-mark {
  opacity: 1;
  border-color: @downy-border;
}

.modal__value {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
